import React from 'react'
import { MDBContainer, MDBRow, MDBCol, MDBTypography, MDBCard, MDBCardBody, MDBCardImage } from 'mdb-react-ui-kit';

const AboutUs = () => {
  const teamMembers = [
    { name: 'Miembro 1', role: 'Rol 1', image: 'https://images.squarespace-cdn.com/content/v1/5b14d6b9e17ba3952cac9f04/1529194285863-CJKZQYFQW19BZULFH1N4/AlebertoCarmona-500x500.jpg' },
    { name: 'Miembro 2', role: 'Rol 2', image: 'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTQffzsaXUhbs8Q1M3DNFfPZTrFXw5llprowR98FghXBw&s' },
    { name: 'Miembro 2', role: 'Rol 2', image: 'https://617mediagroup.com/wp-content/uploads/2019/06/jaclyn-kessel-scaled-500x500.jpg' },
    { name: 'Miembro 2', role: 'Rol 2', image: 'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTuVuikenBS5TFnQmvKgB-WJIVbrM1QSsibzF-Z0Aq-JA&s' },
    // Aade ms miembros aqu
];

  return (
    <MDBContainer className="mt-3">
            <MDBRow>
                <MDBCol>
                    <MDBTypography variant="h1">Acerca de nosotros</MDBTypography>
                </MDBCol>
            </MDBRow>
            <MDBRow className="mt-3">
                <MDBCol>
                    <MDBCard>
                        <MDBCardBody>
                            <MDBTypography variant="h2">Nuestra historia</MDBTypography>
                            <MDBTypography variant="p">
                            Somos una pequea empresa de tecnologa llamada TechWorks, fundada por dos amigos apasionados por la innovacin, Alex y Laura. Nuestra visin es crear productos que no solo sean funcionales, sino tambin impactantes en el mercado.

Comenzamos en un modesto garaje, donde prototipamos nuestra primera invencin: un dispositivo porttil que combinaba las funcionalidades de un telfono inteligente y una computadora personal. Despus de meses de arduo trabajo y pruebas, lanzamos el "TechPad" al mercado y recibimos una respuesta abrumadoramente positiva.

Con el xito del TechPad, TechWorks empez a crecer rpidamente. Contratamos a ingenieros y diseadores talentosos que compartan nuestra pasin por la tecnologa. Pronto, expandimos nuestra lnea de productos para incluir laptops ultradelgadas, wearables inteligentes y sistemas de domtica para hogares conectados.

Lo que nos diferenci fue nuestro enfoque en la experiencia del usuario. Cada producto fue diseado meticulosamente para ser intuitivo y eficiente, brindando a nuestros clientes una sensacin de empoderamiento tecnolgico.

A medida que TechWorks creca, tambin nos comprometimos con la responsabilidad social y ambiental. Implementamos prcticas sostenibles en nuestra cadena de suministro y colaboramos con organizaciones benficas para llevar la tecnologa a comunidades desatendidas.

Con el tiempo, TechWorks se convirti en una marca reconocida a nivel mundial, conocida por su calidad, innovacin y compromiso con la sociedad. Alex y Laura siguen liderando la empresa con pasin y visin, manteniendo viva la chispa que nos llev a iniciar este emocionante viaje tecnolgico.
                            </MDBTypography>
                            
                        </MDBCardBody>
                    </MDBCard>
                </MDBCol>
            </MDBRow>
            <MDBRow className="mt-3">
                <MDBCol>
                    <MDBCard>
                        <MDBCardBody>
                            <MDBTypography variant="h2">Nuestro equipo</MDBTypography>
                            
                            <MDBRow className='g-4'>
                                {teamMembers.map((member, index) => (
                                    <MDBCol key={index}>
                                        <MDBCard style={{ maxWidth: '22rem' }}>
                                        <MDBCol className='text-center'>
                                          <img
                                            src={member.image}
                                            className='rounded-circle'
                                            alt=''
                                            width={100}
                                            height={100}
                                          />
                                        </MDBCol>
                                            <MDBCardBody>
                                                <MDBTypography variant="h5">{member.name}</MDBTypography>
                                                <MDBTypography variant="p">{member.role}</MDBTypography>
                                            </MDBCardBody>
                                        </MDBCard>
                                    </MDBCol>
                                ))}
                            </MDBRow>
                        </MDBCardBody>
                    </MDBCard>
                </MDBCol>
            </MDBRow>
        </MDBContainer>
  )
}

export default AboutUs