import React, { useEffect, useState } from 'react';
import {
  MDBCard,
  MDBCardBody,
  MDBCardTitle,
  MDBCardText,
  MDBCardImage,
  MDBBtn,
  MDBRow,
  MDBCol,
  MDBModal,
  MDBModalDialog,
  MDBModalContent,
  MDBModalHeader,
  MDBModalTitle,
  MDBModalBody,
  MDBModalFooter
} from 'mdb-react-ui-kit';

export default function App() {
  const [basicModal, setBasicModal] = useState(false);
  const [selectArticle, setSelectArticle] = useState([])
  const toggleOpen = () => setBasicModal(!basicModal);

  const articles = [
    { id: 1, image:'https://d1pc5hp1w29h96.cloudfront.net/magefan_blog/5_buenas_razones_para_tener_una_desktop_todo_en_uno.jpg', title: 'Los Avances Tecnolgicos en Computadoras del 2024', content: 'Explora las ltimas innovaciones en tecnologa de computadoras, desde procesadores ms rpidos hasta pantallas de alta resolucin. Descubre cmo estas mejoras estn cambiando la forma en que trabajamos y nos entretenemos.' },
    { id: 2, image:'https://www.silicon.es/wp-content/uploads/2022/04/nuevo-modelo-de-negocio-auge-del-ecommerce-ok-800x445-1-1280x720.jpg', title: 'Gua para Elegir la Computadora Perfecta para Tu Negocio', content: 'Ests buscando una computadora para tu empresa? Esta gua te ayudar a entender qu caractersticas son ms importantes segn tus necesidades, desde la potencia de procesamiento hasta la seguridad de los datos.' },
    { id: 3, image:'https://www.ionos.es/digitalguide/fileadmin/_processed_/2/3/csm_e-commerce_8519c50017.webp', title: 'Cmo Mantener Tu Computadora en ptimas Condiciones', content: 'Consejos prcticos para mantener tu computadora funcionando de manera eficiente y prolongar su vida til. Desde la limpieza adecuada hasta la gestin del almacenamiento, aprende a cuidar tu dispositivo para un rendimiento ptimo.' },
  ];

  useEffect(() => {
    console.log('selectArticle',selectArticle)
  }, [selectArticle])
  
  const handleSelectArticle = (articleId) => {
    const article = articles.filter((article)=>{ return article.id === articleId})
    setSelectArticle(article?.[0])
    toggleOpen()
  }

  return (
    <>
    <MDBRow>
        {articles.map((article) => (
          <>
          <MDBCol key={article.id} md={4} className="mb-4">
            <MDBCard>
              <MDBCardImage src={article.image} position='top' alt='...' />
              <MDBCardBody>
                <MDBCardTitle>{article.title}</MDBCardTitle>
                <MDBCardText>
                  {article.content}
                </MDBCardText>
                <MDBBtn onClick={() => handleSelectArticle(article.id)}>Leer Ms</MDBBtn>
              </MDBCardBody>
            </MDBCard>
          </MDBCol>
          
          <MDBModal open={basicModal} onClose={() => setBasicModal(false)} tabIndex={'-1'}>
            <MDBModalDialog>
              <MDBModalContent>
                <MDBModalHeader>
                  { console.log(selectArticle)}
                  <MDBModalTitle>{selectArticle?.title}</MDBModalTitle>
                  <MDBBtn className='btn-close' color='none' onClick={() => toggleOpen()}></MDBBtn>
                </MDBModalHeader>
                <MDBModalBody>{selectArticle?.content}</MDBModalBody>

                <MDBModalFooter>
                  <MDBBtn color='secondary' onClick={() => toggleOpen()}>
                    Close
                  </MDBBtn>
          
                </MDBModalFooter>
              </MDBModalContent>
            </MDBModalDialog>
          </MDBModal>
          </>
        ))}
    </MDBRow>
    </>
  );
}